<template>
  <div>
    <h2> </h2>

    <div>
      <label for="title"></label>
      <input type="text" id="title" v-model="review.title" /><br />
      <label for="content"></label>
      <textarea type="text" id="content" v-model="review.content" /><br />
    </div>

    <div class="buttons">
      <button @click="modifyReview" class="btn btn-primary"></button>
      <button @click="goReviewDetailView" class="btn btn-danger"></button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "ReviewModify",
  computed: {
    ...mapState(["review"])
  },
  methods: {
    modifyReview() {
      this.$store.dispatch("modifyReview", this.review);

      this.goReviewDetailView();
    },
    goReviewDetailView() {
      this.$router.push(
        "/reviewDetail/" +
          this.$route.params.videoId +
          "/" +
          this.$route.params.reviewSeq
      );
    }
  }
};
</script>

